.questionary__container {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

.questionary__description {
  width: 226px;
  margin: 0;
  margin-bottom: 40px;
  font-family: $Oswald;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  color: #111111;
  text-transform: uppercase;
}

@media (min-width: $tablet-width) {
  .questionary__container {
    width: 768px;
    padding: 0 30px;
  }

  .questionary__description {
    width: 345px;
    margin-left: 40px;
    margin-bottom: 66px;
    font-size: 20px;
    line-height: 1.5;
  }
}

@media (min-width: $desktop-width) {
  .questionary__container {
    width: 1300px;
    padding: 0 40px;
  }

  .questionary__description {
    margin-left: 0;
  }
}
